import React, { useState } from "react"
import axios from "axios"
import './App.css'


const App = () => {
  const [temperature, setTemperature] = useState("")
  const [desc, setDesc] = useState("")
  const [wind, setWind] = useState("")
  const [city, setCity] = useState("Belgrade")
  const [country, setCountry] = useState("RS")

  const getWeatherData = (city, country) => {
    axios({
      method: "GET",
      url: `https://api.openweathermap.org/data/2.5/weather?q=${city},${country}&APPID=6f9f3c22febc914f1639e706d22918db`
    })
      .then((res) => {
       // console.log(response.data.main.temp);
        setTemperature(Math.round(res.data.main.temp - 273.15))
        console.log(res.data)
        setDesc(res.data.weather[0].icon)
        setWind(Math.round(res.data.wind.speed))
      })
      .catch((error) => {
        console.log(error)
      })
  }

  return (
    <div className='wrapper'>
      <div className='header'>
        Weather APP
      </div>
      <br />
      <div className='main-div'>
        <br />
        <input type="text" value={city} onChange={(e) => setCity(e.target.value)}/>
        <input type="text" value={country} onChange={(e) => setCountry(e.target.value)}/>
        <button onClick={() => {getWeatherData(city, country)}}><svg xmlns="http://www.w3.org/2000/svg" fill='white' width="24" height="24" viewBox="0 0 24 24"><path d="M12 2c5.514 0 10 4.486 10 10s-4.486 10-10 10-10-4.486-10-10 4.486-10 10-10zm0-2c-6.627 0-12 5.373-12 12s5.373 12 12 12 12-5.373 12-12-5.373-12-12-12zm2 12l-4.5 4.5 1.527 1.5 5.973-6-5.973-6-1.527 1.5 4.5 4.5z"/></svg></button>
      </div>
        <div className='main-div2'>
          <span> {city.toUpperCase()} , {country.toUpperCase()}</span>
          <br />
          Temperature :<span>{Math.round(temperature * 100) / 100}Â°C </span><br/>
           Wind:<span className='wind'> {wind} </span> m/s
          <br />
         Weather: <img src={`https://openweathermap.com/img/w/${desc}.png`} />
          <br />
          <p>{new Date().toLocaleString().toLowerCase()}</p>
          
        </div>
      
    </div>
  );
};

export default App;
